
/*
whole Container
*/
li{
    list-style-type: none;
}
.grid-container{
    display: grid;
    grid-template-areas:
    "header"
    "main"
    "footer";
    grid-template-columns:  1fr;
    grid-template-rows: 5rem 1fr 5rem;
    height: 100vh;
  }
  .header{
    grid-area: header;
    background-color: #203040;
    color: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
.TitleHome{
   margin-left: auto;
    text-align: center;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.main{
    grid-area: main;
  }
  .footer{
    grid-area: footer;
  }

#homeTitle{
    background-color: white;
    color: black;
    margin: 5vw;
    padding: 2vw;
    font-family: Helvetica, sans-serif;
    font-weight: bolder;
}
h1{
    background-color: white;
    color: black;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
    
}

h4{
    text-align: center;
    background-color: black;
    color: white;
    text-align: center;
    width: 30%;
    padding: 1%;
}
.messageBoxStyle{
    width: 50vw;
    height: 50vh;
}
#blockStyle{
    background-color: black;
    color: white ;
    margin-top: 2vh;
    font-family: helvetica;
    padding: 2vw;
}
/*
Navigation
*/


  
.NaviStyle{
    
  position: fixed;
  top: 0px;
  width: 100vw;
  height: 5vh;
  color:white;
  text-align: center;
  font-family: Helvetica, sans-serif;
  font-weight: bolder;
  float: right;
    
}

.port-pic-container{
    opacity: 100%;
    display: flex;
    float: right;
    width: 20vw;
    height: 20vw;
    margin-top: 10vh;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 20vw;
    border-right: 1px;
    border-style: solid;
    border-right-color: white;
  }

  .flex-container {
    display: flex;
    align-items: stretch;
    background-color: #f1f1f1;
  }
  
  .flex-container > div {
    width: 100px;
    margin: 10px;
    text-align: center;
    line-height: 75px;
  }
