/*
App.js
*/

.bgStyle{
    height: 100vh;
    width: 100vw;
}



/*
HomePage
*/

.homePage {
    overflow-y: scroll; 
    
    padding: 4vw;
    position: static;
    top: 10vh;
    right: 0vh;
    float: right;
    bottom: 10vh;
    margin-right: 5vw;
    width: 75vw;
    height: 80vh;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);   
    -webkit-box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);  
    border-radius: 5px;
    background-color: rgba(255, 255, 255, .15);
    backdrop-filter: blur(5px);
    opacity: 0.5;
    border: 1px;
    border-style: solid;
    border-color:white;
    
}

.logoNavi{
    font-family: helvetica;

}

